.group-chat {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.message-history {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  overflow-y: scroll;
}

.message {
  margin: 0.5rem 0;
  padding: 0.5rem;
  border-radius: 1rem;
}

.user {
  background-color: #e3f2fd;
}

.ai {
  background-color: #f5f5f5;
}

form {
  display: flex;
  align-items: center;
  padding: 1rem;
}

input[type=text] {
  flex: 1;
  margin-right: 1rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
  border: none;
}

button[type=submit] {
  background-color: #2196f3;
  color: white;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
}

button[type=submit]:hover {
  background-color: #1976d2;
}

